d>\n            <td>{{item.company}}</td>\n            <td>{{item.phone}}</td>\n            <td>{{item.leader}}</td>\n            <td>\n              <a href=\"javascript:;\"\n                 @click=\"openShop(item.gid)\">{{item.agent_nums}}</a>\n            </td>\n            <td>\n              <a href=\"javascript:;\"\n                 @click=\"regionNum(item.gid)\">{{item.regions}}</a>\n            </td>\n            <td>\n              <a href=\"javascript:;\"\n                 @click=\"maintain(item.gid)\">{{item.sale_card}}</a>\n            </td>\n            <td>\n              {{item.service_time}}\n            </td>\n            <td>{{item.balance}}</td>\n            <td>{{item.audit_time}}</td>\n          </tr>\n        </tbody>\n      </table>\n\n      <el-dialog title=\"运营商数量\"\n                 @close=\"close\"\n                 :visible.sync=\"DialogVisible\"\n                 width=\"60%\"\n                 center>\n        <table class=\"table table_bottom\">\n          <thead>\n            <tr>\n              <th>运营商名称</th>\n              <th>负责人</th>\n              <th>联系电话</th>\n              <th>运营区域</th>\n              <th>分佣比例</th>\n              <th>合同到期时间</th>\n\n            </tr>\n          </thead>\n          <tbody>\n            <tr v-for=\"item in Operate\">\n              <td>{{item.company}}</td>\n              <td>{{item.leader}}</td>\n              <td>{{item.phone}}</td>\n              <td>{{item.county}}</td>\n              <td>{{item.profit}}</td>\n              <td>{{item.end_time?item.end_time:'---'}}</td>\n            </tr>\n          </tbody>\n        </table>\n        <div class=\"page_center\"\n             v-show=\"pageCounts&&pageCounts>1\">\n          <paging :page-count=\"pageCounts\"\n                  :page=\"OpPage\"\n                  @index=\"OperatorPage\"></paging>\n        </div>\n      </el-dialog>\n      <el-dialog title=\"售卡数量\"\n                 ref=\"my\"\n                 :visible.sync=\"SalecardVisible\"\n                 width=\"60%\"\n                 center\n                 @close=\"close\">\n        <table class=\"table\">\n          <thead>\n            <tr>\n              <th>运营商名称</th>\n              <th>负责人</th>\n              <th>电话</th>\n              <th>售卡总数</th>\n              <th>交易金额</th>\n            </tr>\n          </thead>\n          <tbody>\n            <tr v-for=\"item in Maintain\">\n              <td>{{item.company}}</td>\n              <td>{{item.leader}}</td>\n              <td>{{item.phone}}</td>\n              <td>{{item.sale_card}}</td>\n              <td>{{item.sum}}</td>\n\n            </tr>\n          </tbody>\n        </table>\n        <div class=\"page_center\"\n             v-show=\"maintainCount&&maintainCount>1\">\n          <paging :page-count=\"maintainCount\"\n                  :page=\"maintainPage\"\n                  @index=\"mainPage\"></paging>\n\n        </div>\n      </el-dialog>\n\n      <!-- 分页 -->\n      <div class=\"page_center\"\n           v-show=\"pageCount&&pageCount>1\">\n        <paging :page-count=\"pageCount\"\n                :page=\"page\"\n                @index=\"paging\"></paging>\n        <!--分页的组件-->\n      </div>\n      <Region-dialog title=\"区域个数\"\n                     @regionClose=\"regionClose\"\n                     :Province=\"Province\"\n                     :regionVisible=\"centerDialogVisible\"\n                     :countyShow=\"false\"></Region-dialog>\n    </div>\n  </div>\n</template>\n<script type=\"text/ecmascript-6\">\nimport page from '../common.js';\nimport RegionDialog from '@/components/Dialog/RegionDialog'\nexport default {\n  data () {\n    return {\n      list: [],\n      Operate: [],\n      Salecard: [],\n      Maintain: [],\n      Province: [],\n      token: window.sessionStorage.getItem('bbytoken'),\n      //分页数据\n      page: 1, //显示的页码数\n      OpPage: 1, //运营商数量显示的页码数\n      maintainPage: 1, //保养次数显示的页码数\n      pageCount: 1, //页的总数\n      pageCounts: 1, //运营商数量 页的总数\n      maintainCount: 1,\n      index: 1, //点击的当前页，\n      endpage: 1, //当前一组页码的最大值\n      arr: [], //默认显示 1 2 3 4 5页\n      n: 1, //当前点击的页码的位置\n      authList: [],\n      curId: 0,\n      seCurId: 0,\n      centerDialogVisible: false,\n      DialogVisible: false,\n      SalecardVisible: false,\n      OperatorId: \"\",\n      maintaId: \"\"\n    }\n  },\n  components: {\n    RegionDialog\n  },\n  created () {\n    this.init()\n  },\n\n  mounted () {\n    var id = this.$route.query.id\n\n    this.curId = id;\n    this.$axios.post(\"admin/Auth/erAuth\", {\n      token: this.token,\n      id: id\n    }).then(res => {\n      var arr = res.data.data;\n\n      for (var i = 0; i < arr.length; i++) {\n        if (arr[i].name == \"市级代理列表\") {\n          this.seCurId = arr[i].id\n        }\n      }\n      this.authList = arr;\n    })\n\n  },\n\n  methods: {\n    close () {\n      this.Operate = []\n      this.Maintain = []\n      this.OpPage = 1,\n        this.maintainCount = 1\n      this.pageCounts = 1\n    },\n\n    OperatorPage (e) {\n      this.OpPage = e;\n      this.openShop(this.OperatorId)\n\n    },\n    mainPage (e) {\n\n      this.maintainPage = e;\n      this.maintain(this.maintaId)\n    },\n    paging (e) {\n      this.page = e;\n      this.init()\n    },\n\n    regionClose () {\n      this.centerDialogVisible = false\n    },\n    init () {\n      this.$axios.post(\"supply/index\", { //市级代理列表\n        token: this.token,\n        page: this.page\n      }).then(res => {\n        if (res.data.code == 1) {\n\n          if (res.data.code == 1) {\n            this.list = res.data.data.list\n            this.pageCount = res.data.data.rows;\n          } else {\n            layer.msg(res.data.msg, { time: 500 })\n          }\n        }\n      }).catch(err => {\n        throw (err)\n      })\n    },\n    openShop (gid) {\n      this.OperatorId = gid\n\n      this.$axios.post(\"supply/agent\", { //运营商个数详情\n        token: this.token,\n        gid: gid,\n        page: this.OpPage\n      }).then(res => {\n        if (res.data.code == 1) {\n\n          this.DialogVisible = true\n          this.Operate = res.data.data.list\n          this.pageCounts = res.data.data.rows;\n\n        } else {\n          this.DialogVisible = false;\n          layer.msg(res.data.msg, { time: 500 })\n        }\n      }).catch(err => {\n        throw (err)\n      })\n\n    },\n    regionNum (gid) {\n\n      this.$axios.post(\"supply/region\", {\n        token: this.token,\n        gid: gid\n      }).then(res => {\n\n        if (res.data.code == 1) {\n          this.centerDialogVisible = true\n          this.Province = res.data.data ? res.data.data : [\"\", \"\"];\n\n        } else {\n          this.centerDialogVisible = false;\n          layer.msg(res.data.msg, { time: 500 })\n        }\n      }).catch(err => {\n        throw (err)\n      })\n    },\n    maintain (gid) {\n      this.maintaId = gid\n\n      this.$axios.post(\"supply/card\", {\n        token: this.token,\n        gid: gid,\n        page: this.maintainPage\n      }).then(res => {\n\n        if (res.data.code == 1) {\n          this.SalecardVisible = true\n          this.Maintain = res.data.data.list\n          this.maintainCount = res.data.data.rows;\n        } else {\n          this.SalecardVisible = false;\n          layer.msg(res.data.msg, { time: 500 })\n        }\n\n        //            if(res.data.data.list){\n        //              this.Maintain = res.data.data.list\n        //              this.maintainCount = res.data.data.rows;\n        //            }else {\n        //              this.SalecardVisible=false;\n        //              this.layer.msg(\"\")\n        //            }\n\n      }).catch(err => {\n        throw (err)\n      })\n    },\n    tapImg (gid) {\n\n    },\n\n  },\n\n}\n</script>\n<style scoped>\n.activeClass {\n  color: #04bbfc;\n}\n\n.CityInput {\n  width: 100px;\n  margin-left: 400px;\n  height: 36px;\n  border: 0;\n  outline: none;\n  background-color: #04bbfc;\n  border-radius: 18px;\n  text-align: center;\n}\n\ninput::-webkit-input-placeholder {\n  color: #ffffff;\n}\n\n.Img {\n  vertical-align: middle;\n  width: 100px;\n  height: 120px;\n  padding: 20px 0;\n}\n</style>\n<style>\n.el-dialog__header {\n  background: rgba(152, 209, 249, 1);\n  padding-bottom: 20px;\n}\n\n.el-dialog__title {\n  color: white;\n}\n</style>\n"],"sourceRoot":""}]);

